import { Component } from '@angular/core';
import {IonicPage,NavController, NavParams, ViewController, App } from 'ionic-angular';
import { AppSoundProvider } from '../../providers/app-sound/app-sound';
import { TabsPage } from '../tabs/tabs';
declare const $: any;
@IonicPage()
@Component({
  selector: 'page-chooseCredit-modal',
  templateUrl: 'chooseCredit-modal.html'
})
export class CreditModalPage {
  deviceHeight: any;
  topmar: any;
  constructor(public navCtrl: NavController, public navParams: NavParams,
     public viewCtrl: ViewController, public app: App, public appSound: AppSoundProvider) { }

  ionViewDidLoad() {
    this.deviceHeight = window.screen.height;

    var h = $('.m-div2').height();
    this.topmar = (this.deviceHeight - h) / 2;

  }

  dismissm() {
    this.appSound.play('buttonClick');
    let data
    this.viewCtrl.dismiss(data);
  }
  nothanks() {
    this.appSound.play('buttonClick');
    this.viewCtrl.dismiss();
    //this.navCtrl.push(TabsPage);
    //this.app.getRootNav().push(TabsPage); 
    //let option = [{tabIndex: 1}]
    //this.navCtrl.popToRoot()
    //this.navCtrl.setRoot(TabsPage, {tabIndex: 1});
  }
  find(data: any = "SBC") {
    this.appSound.play('buttonClick');
    this.viewCtrl.dismiss(data)
  }

}



